<template>
    <div class="ranking">
        <img class="top-type-img" src="../../assets/images/gongxianbang.png" alt="">
        <a href="../noteContribute">
            <div class="active-rule">i</div>
        </a>
        <van-tabs sticky class="tab-box" v-model="active" color="#FF2E4D" :lineWidth=20 swipeable>
            <van-tab class="tabs" title="实时">
                <div class="list">
                    <div class="more-three-list">
                        <div class="after-three">
                            <list-li :listData="this.nowList" :type="3"></list-li>
                            <default-page v-show="this.nowList.length<=0"
                                          :img-url="require('../../assets/images/default-data.png')"></default-page>
                        </div>
                    </div>
                </div>
            </van-tab>
            <van-tab title="周榜">
                <div class="list">
                    <div class="more-three-list">
                        <div class="after-three">
                            <list-li :listData="this.weekList" :type="3"></list-li>
                            <default-page v-show="this.weekList.length<=0"
                                          :img-url="require('../../assets/images/default-data.png')"></default-page>
                        </div>
                    </div>
                </div>
            </van-tab>
            <van-tab title="月榜">
                <div class="list">
                    <div class="more-three-list">
                        <div class="after-three">
                            <list-li :listData="this.monthList" :type="3"></list-li>
                            <default-page v-show="this.monthList.length<=0"
                                          :img-url="require('../../assets/images/default-data.png')"></default-page>
                        </div>
                    </div>
                </div>
            </van-tab>
        </van-tabs>
    </div>
</template>

<script>
  import ListLi from '../../components/ListBoard'
  import defaultPage from '../../components/DafultPage'

  export default {
    name: "DetailRanking",
    components: {
      ListLi, defaultPage
    },
    data() {
      return {
        active: 0,
        afterThree: [],
        nowList: [],
        weekList: [],
        monthList: []
      }
    },
    mounted: function () {
      this.getNowList();
      this.getWeekList();
      this.getMonthList();
    },
    created() {
      document.title = '榜单详情';
    },
    methods: {
      getNowList: function () {
        this.$axios({
          method: 'get',
          url: process.env.VUE_APP_BASE5 + '/userContribute/queryUserContribute'// 贡献榜
        }).then((res) => {
            if (res.data.status !== 1000) {
              return false;
            } else if (res.data.data !== null) {
              this.nowList = res.data.data;
            }
          }
        ).catch((err) => {
          console.log(err);
        })
      },
      getWeekList: function () {
        this.$axios({
          method: 'get',
          url: process.env.VUE_APP_BASE5 + '/userContribute/getWeekUserContribute' // 周榜
        }).then((res) => {
            if (res.data.status !== 1000) {
              return false;
            } else if (res.data.data !== null) {
              this.weekList = res.data.data;
            }
          }
        ).catch((err) => {
          console.log(err);
        })
      },
      getMonthList: function () {
        this.$axios({
          method: 'get',
          url: process.env.VUE_APP_BASE5 + '/userContribute/getMonthUserContribute' //月榜
        }).then((res) => {
            if (res.data.status !== 1000) {
              return false;
            } else if (res.data.data !== null) {
              this.monthList = res.data.data;
            }
          }
        ).catch((err) => {
          console.log(err);
        })
      }
    },
    computed: {}
  }
</script>

<style scoped lang="scss">

    .van-tab__pane {
        overflow: hidden;
    }

    /deep/ .van-ellipsis {
        text-align: center;
    }

    /deep/ .van-tab--active {
        color: #FF2E4D;
    }

    .ranking {
        background: #fff;
        min-height: 100vh;
    }

    .active-rule {
        height: .52rem;
        line-height: .55rem;
        text-align: center;
        width: .52rem;
        font-size: .4rem;
        /*background: rgb(35, 7, 85);*/
        background: rgba(255, 255, 255, .5);
        color: #fff;
        position: absolute;
        right: .6rem;
        top: .4rem;
        z-index: 3;
        border-radius: 50%;
        padding: 0 .1rem;
    }

    .list {
        width: 93.5%;
        height: 100%;
        overflow: hidden;
        margin: 0 auto .32rem;
        position: relative;
    }

    .more-three-list {
        width: 100%;
        height: 100%;
        overflow: hidden;
        margin: 0 auto;
        .top-three {
            height: 5.56rem;
            width: 100%;
            background: url("../../assets/images/list_details_top_bg.png") center no-repeat;
            background-size: 100%;
            display: flex;
            justify-content: space-around;
            align-items: flex-end;
            position: relative;
            overflow: hidden;
            .white-radius {
                width: 40rem;
                height: 40rem;
                background: #fff;
                position: absolute;
                bottom: -39.7rem;
                left: 50%;
                margin-left: -20rem;
                border-radius: 50%;
            }
        }
        .after-three {
            width: 100%;
            /*border: .08rem solid #B949E6;*/
            border-top: none;
            -webkit-border-radius: 0 0 .1rem .1rem;
            -moz-border-radius: 0 0 .1rem .1rem;
            border-radius: 0 0 .1rem .1rem;
            background: #fff;
        }
    }

    .less-three-list {
        width: 100%;
        height: 100%;
        overflow: hidden;
        margin: 0 auto;
        background: #fff;
        padding: 0 .2rem;
        border: .08rem solid #B949E6;
        -webkit-border-radius: .1rem;
        -moz-border-radius: .1rem;
        border-radius: .1rem;

    }

    .top-type-img {
        height: 4.45rem;
        width: 100%;
    }
</style>
