<template>
    <div class="list-board">
        <!--两行并且有播放的-->
        <div v-if="type===0" class="three-list-li" v-for="(item,index) in localData" :key="index">
            <div class="ranking">{{index+1}}</div>
            <img class="header-img" v-if="item.userHeadUrl" :src="item.userHeadUrl" alt="">
            <img class="header-img" v-if="item.singerHeadUrl" :src="item.singerHeadUrl" alt="">
            <img class="header-img" v-if="item.lyricImgUrl" :src="item.lyricImgUrl" alt="">

            <!--<img class="header-img" :src="item.singerHeadUrl?item.singerHeadUrl:item.lyricImgUrl" alt="头像">-->
            <div class="star-hots">
                <p class="song-name">{{item.songName}}</p>
                <p class="supNum">{{item.singerName}} <span class="iconfont icon-shipinbofangyingpian view-num">{{ item.playNum}}</span>
                </p>
            </div>
            <div class="play-btn iconfont icon-bofang" @click="$isApp"></div>
        </div>
        <!-- 两行并且有播放的 並且有箭頭的 -->
        <div v-if="type===1" class="three-list-li" v-for="(item,index) in localData" :key="index">
            <div class="ranking">{{index+1}}</div>
            <!--<img class="header-img" :src="item.singerHeadUrl" alt="头像">-->
            <img class="header-img" v-if="item.userHeadUrl" :src="item.userHeadUrl" alt="">
            <img class="header-img" v-if="item.singerHeadUrl" :src="item.singerHeadUrl" alt="">
            <img class="header-img" v-if="item.lyricImgUrl" :src="item.lyricImgUrl" alt="">
            <div class="star-hots">
                <p class="song-name">{{item.songName}}</p>
                <p class="supNum">{{item.singerName}}</p>
            </div>
            <div class="new-color" v-if="!item.lastRank">new</div>
            <div class="up-color" v-else :class="{'down-color':item.nowRank-item.lastRank<0}">
                {{item.nowRank-item.lastRank>0?'↑':'↓'}}
            </div>
            <div class="rank-range" v-if="item.lastRank">{{item.nowRank-item.lastRank}}</div>
            <div class="play-btn iconfont icon-bofang" @click="$isApp"></div>
        </div>
        <!--两行 -->
        <div v-if="type===2" class="three-list-li" v-for="(item,index) in localData" :key="index">
            <div class="ranking">{{index+1}}</div>
            <img class="header-img" v-if="item.userHeadUrl" :src="item.userHeadUrl" alt="">
            <img class="header-img" v-if="item.singerHeadUrl" :src="item.singerHeadUrl" alt="">
            <img class="header-img" v-if="item.lyricImgUrl" :src="item.lyricImgUrl" alt="">
            <!--<img class="header-img" :src="item.singerHeadUrl?item.singerHeadUrl:item.lyricImgUrl" alt="头像">-->
            <div class="star-hots" style="width: 71%" v-if="item.lyricImgUrl">
                <p class="song-name">{{item.songName}}</p>
                <p class="supNum">{{item.singerName}}</p>
            </div>
            <div class="star-hots" v-else>
                <p class="song-name">{{item.singerName}}</p>
                <p class="supNum">人气值：{{item.popularityNum}}</p>
            </div>
            <!--<div class="play-btn iconfont icon-bofang" @click="$isApp"></div>-->
        </div>
        <!--一行并且无播放的-->
        <div v-if="type===3" class="three-list-li" v-for="(item,index) in localData" :key="index">
            <div class="ranking">{{index+1}}</div>
            <div class="header-img-box">
                <img class="header-img" v-if="item.userHeadUrl" :src="item.userHeadUrl" alt="">
                <div class="v-point" v-if="item.isVip===1">v</div>
            </div>
            <img class="header-img" v-if="item.singerHeadUrl" :src="item.singerHeadUrl" alt="">
            <img class="header-img" v-if="item.lyricImgUrl" :src="item.lyricImgUrl" alt="">
            <!--<img class="header-img" :src="item.singerHeadUrl?item.singerHeadUrl:item.lyricImgUrl" alt="头像">-->
            <div class="star-hots">
                <p style="height: 100%;line-height: 1.16rem" :class="{vipStatus:item.isVip===1}">{{item.userName}}</p>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
    name: "ListBoard",
    props: {
      listData: Array,
      type: [String, Number]
    },
    computed: {
      localData: function () {
        for (let i = 0; i < this.listData.length; i ++) {
          if (this.listData[i].lyricImgUrl === null || this.listData[i].userHeadUrl === null || this.listData[i].singerHeadUrl === null) {
            this.listData[i].lyricImgUrl = require('../assets/images/default-header.jpg');
          }
        }
        return this.listData;
      }
    },
    mounted() {

    }
  }
</script>

<style scoped lang="scss">
    @mixin radius50 {
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        border-radius: 50%;
    }

    .play-btn {
        flex: 0 0 auto;
        height: .42rem;
        width: .42rem;
        background: #FF2E4D;
        line-height: .42rem;
        color: #fff;
        text-align: center;
        font-size: .25rem;
        @include radius50;
    }

    .vipStatus {
        color: #FF2E4D !important;
    }

    .header-img-box {
        position: relative;
        .v-point {
            height: .3rem;
            width: .3rem;
            position: absolute;
            right: .15rem;
            bottom: .15rem;
            line-height: .25rem;
            text-align: center;
            color: yellow;
            display: block;
            background: #FF2E4D;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            border-radius: 50%;
            border: 1px solid #fff;
        }
    }

    .three-list-li {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
        margin: 0 auto;
        height: 1.16rem;
        line-height: 1.16rem;
        text-align: center;
        font-size: .25rem;
        position: relative;
        border-bottom: 1px solid #F7F7F7;
        &:nth-of-type(1) .ranking {
            color: #FFAB26;
        }
        &:nth-of-type(2) .ranking {
            color: #4FA6E7;
        }
        &:nth-of-type(3) .ranking {
            color: #FF2E4D;
        }
        .ranking {
            flex: 0 0 auto;
            width: .7rem;
            text-align: center;
            font-size: .34rem;
            color: #909090;
        }

        .header-img {
            flex: 0 0 auto;
            display: block;
            width: .73rem;
            height: .73rem;
            margin: .143rem .2rem .143rem 0;
            @include radius50;
        }
        .star-hots {
            flex: 0 0 auto;
            width: 55%;
            height: 100%;
            p {
                height: 50%;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                &:nth-of-type(1) {
                    line-height: .8rem;
                    font-size: .3rem;
                    color: #333333;
                    font-weight: bold;
                }
                &:nth-of-type(2) {
                    line-height: .5rem;
                    font-size: .28rem;
                    color: #909090;
                }
            }
        }
    }

    .view-num {
        font-size: .25rem;
        margin: 0 .15rem 0 .15rem;
        padding: 0 .2rem 0 0;
    }

    .up-color {
        height: 100%;
        color: #FF2E4D;
        font-weight: 600;
        line-height: 1.1rem;
    }

    .down-color {
        color: #4FA6E7;
    }

    .rank-range {
        padding: 0 .2rem;
    }

    .new-color {
        color: #77C04B;
        padding: 0 .2rem;
    }
</style>
